import './App.css';
import TextBucks from './TextBucks';
import { useState } from 'react';

function App() {
  const [count, setCount] = useState(0);

  return (
    <div className="App">
      <header className="App-header">
        <p>{count}</p>
        <button onClick={() => setCount(count + 1)}>
        Click me
        </button>
        <hr style={{
          width: 100
        }}/>
        {TextBucks(count)}
      </header>
    </div>
  );
}

export default App;
